import React from "react";
import {AppBar, Button, Toolbar} from "@mui/material";
import {Link} from "react-router-dom";

const Navbar = () => {
    return (
        <>
            <AppBar position="fixed" color="primary">
                <Toolbar>
                    <Button color="inherit" component={Link} to="/"></Button>
                    {/*<Button color="inherit" component={Link} to="/projects">  </Button>*/}
                    <Button color="inherit" component={Link} to="/documents">  </Button>
                    {/*<Button color="inherit" component={Link} to="/trial-and-error">  </Button>*/}
                    {/*<Button color="inherit" component={Link} to="/toys">  </Button>*/}
                </Toolbar>
            </AppBar>
            <Toolbar/>
        </>
    );
};

export default Navbar;
